<template>
  <div>
    <div class="tool-container" v-if="isShowToolBox">
      <span class="she-btn">设置</span>
      {{title}}
    </div>
    <div class="tool-container-search" v-else>
      <div class="bsx-logo"></div>
      <div class="input-box">
        <div class="input-placeholder" v-if="!isFocus">任务搜索</div>
        <input type="text" @focus="handleFouces" @blur="handleBlur">
      </div>
      <div class="public-task-btn">发需求</div>
    </div>
    <app-main @updateRoute="updateRoute"></app-main>
    <nav-bar :routerName="routerName"/>
  </div>
</template>

<script>
  import { AppMain, NavBar } from './components'
  export default {
    name: 'llll',
    data() {
      return {
        isShowToolBox: true,
        title: '',
        routerName: 'home',
        isFocus: false
      }
    },
    components: {
      AppMain,
      NavBar
    },
    methods: {
      handleFouces() {
        this.isFocus = true
      },
      handleBlur() {
        this.isFocus = false
      },
      updateRoute() {
        if (this.$route.meta.isShowToolBox !== undefined && this.$route.meta.isShowToolBox === false) {
          this.isShowToolBox = false
        } else {
          this.isShowToolBox = true
          this.title = this.$route.meta.title
        }
        this.routerName = this.$route.name
      }
    },
    created() {
      this.updateRoute()
    }
  }
</script>

<style scoped>

</style>



